.review {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.review__stars {
  display: flex;
  margin: 0;
  padding: 0;
  gap: 4px;
}

.review__star {
  background: url(/images/star.svg) no-repeat;
  background-position: center;
  height: 16px;
  width: 16px;
  list-style: none;
}

.review__stars--1 .review__star:nth-child(-n+1),
.review__stars--2 .review__star:nth-child(-n+2),
.review__stars--3 .review__star:nth-child(-n+3),
.review__stars--4 .review__star:nth-child(-n+4),
.review__stars--5 .review__star:nth-child(-n+5) {
  background: url(/images/star-active.svg) no-repeat;
  background-position: center;
}

.review__numbers {
  color: var(--text-accent-color);
}
