.card {
  padding: 32px 16px 16px;
  width: 200px;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.card__image {
  display: block;
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__product-name {
  font-size: 12px;
  font-weight: 500;
  margin-top: 40px;
  color: var(--text-accent-color);
  line-height: 18px;
}

.card__product-code {
  margin-top: 4px;
}

.card__review {
  margin-top: 16px;
}

.card__price {
  margin-top: 24px;
}

.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: var(--main-color);
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
}

.card__button:hover {
  background-color: #fff;
  color: var(--main-color);
  border: 1px solid var(--main-color);
}
